import { graphql, useStaticQuery } from 'gatsby'

const useBlogMetadata = () => {
  const blog = useStaticQuery(
    graphql`
      query blog_METADATA_QUERY {
        allMarkdownRemark {
            edges {
              node {
                frontmatter {
                  title
                  description
                }
              }
            }
          }
      }
    `
  )
  console.log(blog.allMarkdownRemark.edges.node)
  return blog.allMarkdownRemark.edges.node
}

export default useBlogMetadata
